.all-padge {
  position: relative;
  width: 100%;
  height: 22vh;
  color: white;
}

.all-svg {
  display: flex;
  justify-content: space-between;
  margin-top: 300px;
}


#svg circle {
  stroke-dashoffset: 0;
  transition: stroke-dashoffset 1s linear;
  stroke: #0c21e6;
  stroke-width: .5em;

}


#cont,
#cont1,
#cont2,
#cont3,
#cont4 {
  text-align: center;
  display: block;
  height: 200px;
  width: 200px;
  margin: 2em auto;
  box-shadow: 0 0 1em black;
  border-radius: 100%;
  position: relative;
}





#cont:after {

  position: absolute;
  display: block;
  height: 100px;
  width: 100px;
  left: 50%;
  top: 50%;
  box-shadow: inset 0 0 1em black;
  content: attr(data-pct)"html";
  margin-top: -80px;
  margin-left: -80px;
  border-radius: 100%;
  line-height: 160px;
  font-size: 2em;
  text-shadow: 0 0 0.5em black;
}


